import { loadData } from '@/api/common'
import Scroll from '@/components/scroll'
export default {
  components: {
    Scroll
  },
  data() {
    return {
      listParameter: {
        id: '',
        contactName: '',
        contactPhone: '',
        addressDetail: ''
      }
    }
  },
  created() {
    this.listDate()
  },
  methods: {
    listDate() {
      this.listParameter.id = this.$route.params.id
      loadData('/member/address/detail/' + this.$route.params.id).then(data => {
        console.log(data)
        this.listParameter.contactName = data.data.contactName
        this.listParameter.contactPhone = data.data.contactPhone
        this.listParameter.addressDetail = data.data.addressDetail
      })
    },
    save() {
      loadData('/member/address/save', this.listParameter).then(data => {
        this.$router.push('/memberCenter/components/addressManagement')
      })
    }
  }
}



